.bg{
    background-image: url('../../images/airp.jpg') no-repeat ;
    width: 100%;
    height:100vh;
    background-size: 100px;
}
.form-container{
    position: absolute;
    top:15vh;
    background: #fff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px #000;

}
.s-form-container{
    position: relative;
    top: 2em;
    background: #fff;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 0px 0px 10px #000;

}